*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --navbar-min-max-width: 260px;
    --body-min-max-width: calc(100% - 260px);
    --navbar-bg-color: #202123;
    --chat-room-bg-color: #343541;
    --z-index-hover: 10;
}
.HomeContainer{
    display: flex;
    width: 100%;
    height: 100vh;
    color: white;
}
.NavbarContainer{
    max-width: var(--navbar-min-max-width);
    min-width: var(--navbar-min-max-width);
    background-color: var(--navbar-bg-color);
}
.chatContainer{
    background-color: var(--chat-room-bg-color);
    min-width: var(--body-min-max-width);
    justify-content: center;
    max-width: var(--body-min-max-width);
}
.NavbarContainer,
.chatContainer{
    padding: .5% .2%;
}
.NewChat{
    border: 1px solid rgba(177, 177, 177, 0.293);
    font-weight: 300;
    font-size: 15.5px;
    display: flex;
    align-items: center;
    border-radius: 7px;
    padding:13px 5px;
    cursor: pointer;
}
.NewChat:hover{
    background-color: hsla(240,9%,59%,.1);
    transition: .2s;
}
.NewChat span{
    margin-left:15px;
}
.recentChats{
    display: flex;
    margin: 8px 5px;
    overflow: hidden;
    flex-direction: column;
    word-wrap: none;
    flex: 1 1 0%;
}
.recentChats,
.NewChat,
.footer,
.userQuery,
.answerQuery{
    width: 97%;
}
.recentChats .title,
.footerContent{
    border-radius: 5px;
    width: 98%;
    flex: 1 1 0%;
    overflow: hidden;
    display: flex;
    position: relative;
    margin: 5px 0;
    word-break: break-all;
    cursor: pointer;
    margin-left: 5px;
    text-overflow: ellipsis;
    font-weight: 300;
    font-size: 16px;
}
.title{
    font-style: italic;
}
.title,
.footer{
    padding:12px 10px;
}
.title:hover,
.footerContent:hover{
    --tw-bg-opacity: 1;
    --z-index-hover:-10;
    background-color: rgba(42,43,50,var(--tw-bg-opacity));
}
.from-gray-900 {
    --tw-gradient-from: #202123;
    position: absolute;
    right: 0;
    width: 2rem;
    z-index: var(--z-index-hover);
    height: 100%;
    background-image: linear-gradient(to left,var(--tw-gradient-stops));
    --tw-gradient-to: rgba(32,33,35,0);
    --tw-gradient-stops: var(--tw-gradient-from),var(--tw-gradient-to);
}
.footer{
    margin: 0% 0;
    display: flex;
    flex-direction: column;
    position: relative;
}
.footer::before{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.378);
}
.footerContent,
.contentDiv{
    padding: 12px 10px;
}
.chatContentContainer{
    width: 100%;
    max-height: 89.7vh;
    /* margin-bottom: 5%; */
}
.chatContentContainer,
.contentSuggestions,
.NavbarContainer,
.chatContainer{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.relative{
    position: relative;
}
.absolute{
    position: absolute;
}
.bottom-2{
    bottom: 0%;
}
.top-border{
    border-top: 1px solid white;
}
.z-index-10{
    z-index: 10;
}
.logo{
    font-size: 35px;
    font-weight: 700;
    margin: 1% 0;
    margin-bottom: 5%;
}
.contentSuggestions,
.contentDiv{
    width: 68%;
}
.flex-column-center{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.flex-wrap{
    flex-wrap: wrap;
    flex-direction: row;
}
.flex-row-center{
    display: flex;
    justify-content: center;
}
.flex-1{
    flex: 1;
}
.margin-1{
    margin: 1%;
}
.contentDiv{
    width: 90%;
    border-radius: 5px;
    background-color: hsla(0,0%,100%,.05);
    margin-top: 20px;
    cursor: pointer;
}
.contentDiv:hover{
    --tw-bg-opacity: 1;
    background-color: rgba(32,33,35,var(--tw-bg-opacity));
}
.title{
    margin: 0;
}
.chatFooter{
    display: flex;
    width: 100%;
    justify-content: center;
    background-color: var(--chat-room-bg-color);
    /* box-shadow: -0px -10px 10px 2px rgba(0, 0, 0, 0.437); */
    /* filter: blur(1px); */
    background-color: transparent;
}
.input{
    width: 100%;
    position: relative;
    background-color: transparent;
}
.inputDiv,
.inputDiv textarea{
    width: 70%;

}
.inputDiv textarea{
    resize: none;
    font-size: 18px;
    overflow: none;
    bottom: none;
    width: 80% ;
    border-radius: 5px;
    --tw-bg-opacity: 1;
    background-color: rgba(64,65,79,var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgba(255,255,255,var(--tw-text-opacity));
    padding: 1% 1%;
    --tw-shadow: 0 0 15px rgba(0,0,0,.1);
    --tw-shadow-colored: 0 0 15px var(--tw-shadow-color);
    box-shadow: 0 0 transparent,0 0 transparent,var(--tw-shadow);
    box-shadow: var(--tw-ring-offset-shadow,0 0 transparent),var(--tw-ring-shadow,0 0 transparent),var(--tw-shadow);
}
textarea:focus{
    padding: 1% 1%;
    --tw-shadow: 0 0 15px rgba(0,0,0,.1);
    --tw-shadow-colored: 0 0 15px var(--tw-shadow-color);
    box-shadow: 0 0 transparent,0 0 transparent,var(--tw-shadow);
    box-shadow: var(--tw-ring-offset-shadow,0 0 transparent),var(--tw-ring-shadow,0 0 transparent),var(--tw-shadow);
    outline: transparent;
}
.input div{
    padding-top:1% ;
}
.name{
    padding: 0.3% 0 0.2% 0;
    padding-top: .3% !important;
}
.chatInterface{
    width: 100%;
    font-size: 1rem;
    line-height: 1rem;
    overflow-y: auto;
}
.chatInterface::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}
.answerQuery{
    background-color: rgb(68 70 84);
    border-radius: 5px;
}
.userQuery,
.answerQuery{
    margin: 1% 0;
    padding: 1% 3%;
}
.flex{
    display: flex;
}
.flex-direction-row{
    flex-direction: row;
}
.align-items-center{
    align-items: center;
}

.userImg{
    height: auto;
    width: 35px;
    background: cyan;
    padding: 4px;
    border-radius: 4px;
}
.userImg img{
    width: 100%;
    height: 100%;
}
.userImg,
.userQuestion{
    margin: 0 1%;
}
.userQuestion{
    padding: 1px;
    width: 70%;
    text-align: justify;
}
.chats{
    width: 100%;
}
.flex-start{
    justify-content: flex-start;
}
.input::before{
    content: '';
    position: absolute;
    width: 60%;
    bottom: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.328);
    z-index: -10;
    filter: blur(15px);
}

/* after material ui */
.rotate{
    animation: rotate 1s infinite ;
    color: cyan !important;
}
@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}